.button {
  width: 100%;
  border: 2px solid @light-black;
  color: @light-black;
  padding: 16px 0 16px 0;
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: @white;
}

.button:hover {
  background-color: @light-black;
  color: @white;
}

.button:active {
  background-color: @light-black;
  color: @white03;
}
